export function getHTMLElement(parentElement: HTMLElement | Document, selector: string): HTMLElement | null {
  try {
    // ä½¿ç”¨ querySelector æŸ¥æ‰¾å…ƒç´ 
    const element = parentElement.querySelector(selector);

    // å¦‚æœæ²¡æœ‰æ‰¾åˆ°ï¼ŒæŠ›å‡ºå¼‚å¸¸
    if (!element) {
      throw new Error(`æœªæ‰¾åˆ°åŒ¹é…çš„å…ƒç´ ï¼Œé€‰æ‹©å™¨: "${selector}"`);
    }

    // å¦‚æœæ‰¾åˆ°çš„å…ƒç´ ä¸æ˜¯ HTMLElement ç±»å‹ï¼Œä¹ŸæŠ›å‡ºå¼‚å¸¸
    if (!(element instanceof HTMLElement)) {
      throw new Error(`é€‰æ‹©å™¨ "${selector}" è¿”å›çš„å…ƒç´ ä¸æ˜¯ HTMLElement ç±»å‹`);
    }

    return element;
  } catch (error) {
    console.error("getHTMLElement å‘ç”Ÿé”™è¯¯:", error);
    return null;
  }
}

