/**
 * æ£€æµ‹æ˜¯å¦å­˜åœ¨æŒ‡å®šçš„ HTMLElement å…ƒç´ 
 * @param parentElement çˆ¶å®¹å™¨ï¼Œå¯ä»¥æ˜¯ HTMLElement æˆ– Document
 * @param selector CSS é€‰æ‹©å™¨å­—ç¬¦ä¸²ï¼Œç”¨äºå®šä½å…ƒç´ 
 * @returns å¦‚æœåŒ¹é…åˆ°å…ƒç´ ä¸”å±äº HTMLElement ç±»å‹ï¼Œåˆ™è¿”å› trueï¼Œå¦åˆ™è¿”å› false
 */
export function existsHTMLElement(parentElement: HTMLElement | Document, selector: string): boolean {
  const element = parentElement.querySelector(selector);
  return !!(element && element instanceof HTMLElement);
}

/**
 * æ ¹æ®é€‰æ‹©å™¨æŸ¥æ‰¾ HTMLElement å…ƒç´ 
 * @param parentElement çˆ¶å®¹å™¨ï¼Œå¯ä»¥æ˜¯ HTMLElement æˆ– Document
 * @param selector CSS é€‰æ‹©å™¨å­—ç¬¦ä¸²
 * @returns æ‰¾åˆ°çš„ HTMLElement æˆ– null
 */
export function findHTMLElement(parentElement: HTMLElement | Document, selector: string): HTMLElement | null {
  const element = parentElement.querySelector(selector);
  return (element && element instanceof HTMLElement) ? element : null;
}

/**
 * ç”Ÿæˆä¸€ä¸ªå”¯ä¸€çš„ ID
 * @returns ä¸€ä¸ªå”¯ä¸€çš„å­—ç¬¦ä¸² ID
 */
export function generateUniqueId(): string {
  // å¦‚æœæ”¯æŒ crypto.randomUUIDï¼Œç›´æ¥ä½¿ç”¨
  if (typeof crypto !== 'undefined' && crypto.randomUUID) {
    return crypto.randomUUID();
  } else {
    // å¤‡ç”¨æ–¹æ¡ˆï¼šä½¿ç”¨å½“å‰æ—¶é—´æˆ³å’Œéšæœºæ•°ç”Ÿæˆå”¯ä¸€ ID
    const timestamp = Date.now().toString(36);
    const randomPart = Math.random().toString(36).substring(2, 10);
    return `${timestamp}-${randomPart}`;
  }
}